    animation: fa-spin 1s infinite steps(8);
    font-size: 30px
}

.biab_body .biab_console-log {
    position: absolute;
    top: 0;
    left: 0;
    width: 95%;
    height: 100px;
    padding: 10px;
    overflow-x: hidden;
    overflow-y: scroll;
    background: #fff;
    border-bottom: 1px solid #aaa;
    font-size: 12px;
