.biab_body .tooltip {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1060;
    display: none;
    max-width: 270px;
    padding: 1px;
    line-height: 1.5;
    text-decoration: none;
    text-shadow: none;
    text-transform: none;
    word-break: word;
    font-size: 12px;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ccc;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2)
}

.biab_body .tooltip b,
.biab_body .tooltip strong {
    font-weight: bold
}

.biab_body .tooltip.top {
    margin-top: -10px
}

.biab_body .tooltip.right {
    margin-left: 10px
}

.biab_body .tooltip.bottom {
    margin-top: 10px
}

.biab_body .tooltip.left {
    margin-left: -10px
}

.biab_body .tooltip-inner {
    padding: 5px 10px
}

.biab_body .tooltip>.tooltip-arrow,
.biab_body .tooltip>.tooltip-arrow:after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid
}

.biab_body .tooltip>.tooltip-arrow {
    border-width: 11px
}

.biab_body .tooltip>.tooltip-arrow:after {
    border-width: 10px;
    content: ""
}

.biab_body .tooltip.top>.tooltip-arrow {
    left: 50%;
    margin-left: -11px;
    border-bottom-width: 0;
    border-top-color: #999999;
    border-top-color: rgba(0, 0, 0, 0.25);
    bottom: -11px
}

.biab_body .tooltip.top>.tooltip-arrow:after {
    content: " ";
    bottom: 1px;
    margin-left: -10px;
    border-bottom-width: 0;
    border-top-color: #fff
}

.biab_body .tooltip.right>.tooltip-arrow {
    top: 50%;
    left: -11px;
    margin-top: -11px;
    border-left-width: 0;
    border-right-color: #999999;
    border-right-color: rgba(0, 0, 0, 0.25)
}

.biab_body .tooltip.right>.tooltip-arrow:after {
    content: " ";
    left: 1px;
    bottom: -10px;
    border-left-width: 0;
    border-right-color: #fff
}

.biab_body .tooltip.bottom>.tooltip-arrow {
    left: 50%;
    margin-left: -11px;
    border-top-width: 0;
    border-bottom-color: #999999;
    border-bottom-color: rgba(0, 0, 0, 0.25);
    top: -11px
}

.biab_body .tooltip.bottom>.tooltip-arrow:after {
    content: " ";
    top: 1px;
    margin-left: -10px;
    border-top-width: 0;
    border-bottom-color: #fff
}

.biab_body .tooltip.left>.tooltip-arrow {
    top: 50%;
    right: -11px;
    margin-top: -11px;
    border-right-width: 0;
    border-left-color: #999999;
    border-left-color: rgba(0, 0, 0, 0.25)
}

.biab_body .tooltip.left>.tooltip-arrow:after {
    content: " ";
    right: 1px;
    border-right-width: 0;
    border-left-color: #fff;
    bottom: -10px
