    z-index: 999
}

.biab_body .biab_inline-placement-tooltip-item {
    cursor: pointer;
    display: inline-block;
    font-weight: 700;
    padding: 2px 10px
}

.biab_body .biab_inline-placement-tooltip-item::after {
    content: '';
    position: absolute;
    height: 20px;
    bottom: 100%;
    left: 50%;
    pointer-events: none;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%)
}

