    position: relative;
    background-color: #289FD3;
    border-radius: 0;
    background-clip: padding-box;
    outline: 0;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    overflow: auto;
    max-height: 100%;
    word-wrap: break-word
}

